import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import React from 'react';

import fetchJsonp from 'fetch-jsonp';

import { Friends } from './api';

import Home from './panels/Home';
import Persik from './panels/Persik';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			access_token: '',
			friendsIds: null,
			userInfo: []
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				case 'VKWebAppAccessTokenReceived':
					this.setState({ access_token: e.detail.data.access_token });
					break;
				case 'VKWebAppAccessTokenFailed':
					console.error('access_token:', e.detail.data);
					break;
				default:
					console.log(e.detail.type);
			}
		});
		
		connect.send("VKWebAppGetAuthToken", {"app_id": 6940370, "scope": "friends,status,wall,groups"});
		connect.send('VKWebAppGetUserInfo', {});

		this.getRequests();
	}

	getRequests() {
		return Friends.getRequests({
			count: 1000,
			out: 1,
			access_token: this.state.access_token
		})
			.then(data => {
				console.log(data);
				this.setState({ friendsIds: data.response.items });
				this.getUserInfo()
			})
			.catch(e => console.error(e));
	}
	getUserInfo () {
		let strFriendsIds = this.state.friendsIds.slice(0, 100).join(',');
		let url = `https://api.vk.com/method/users.get?v=5.52&access_token=${this.state.access_token}&user_ids=${strFriendsIds}&fields=photo_200_orig,status`;
		return fetchJsonp(url)
			.then(res => res.json())
			.then(data => {
				this.setState((state) => {
					state.userInfo.push(...data.response);
				});
				console.log(this.state.userInfo)
			})
			.catch(e => console.error(e));
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} access_token={this.state.access_token} />
				<Persik id="persik" go={this.go} access_token={this.state.access_token} userInfo={this.state.userInfo} />
			</View>
		);
	}
}

export default App;
