import { Panel, ListItem, Button, Group, Div, Avatar, PanelHeader, HeaderButton, Cell, IOS } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import React from 'react';

import PropTypes from 'prop-types';

class Friends extends React.Component {
	constructor(props) {
		super(props);
	}
	render() {
		return (
			<Panel id={this.props.id}>
				<PanelHeader
					left={<HeaderButton onClick={this.props.go} data-to="home">
						{this.osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
					</HeaderButton>}
				>
					Панель управления.
				</PanelHeader>
				<Group title="Standart size">
					{
						this.props.userInfo.map(friend => {
							return (
								<Cell
									size="l"
									description="VKontakte"
									before={<Avatar src={friend.photo_200_orig} />}
									bottomContent={<Button>Удалить</Button>}
									key={friend.id}
								>
								Артур Стамбульцян
								</Cell>
							);
						})
					}
				</Group>
			</Panel>
		);
	}
}

Friends.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	userInfo: PropTypes.array.isRequired,
	access_token: PropTypes.string.isRequired
};

export default Friends;
